.textC {
  width: 100%;
  margin-left: auto;
  margin-right: auto;
  text-align: center;
  position: absolute;
  margin-top: 3.5in;
}

.textC h1:nth-child(1) {
  color: white;
  font-family: "Fira Code", monospace;
  font-weight: 800;
  font-size: 30px;
  margin: 0 0 0 35%;
  text-align: left;
  white-space: nowrap;
  overflow: hidden;
  width: 170px;
  -webkit-animation: type 2s steps(40, end) forwards;
          animation: type 2s steps(40, end) forwards;
}

.textC h1:nth-child(2) {
  opacity: 0;
  font-family: "Work Sans", sans-serif;
  margin: 0 auto auto auto;
  background: linear-gradient(to right, #ff497c, #ff497c, #ff497c);
  font-weight: 800;
  font-size: 100px;
  width: 600px;
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  overflow: hidden;
  -webkit-animation: shring-animation 2.2s steps(40, end) 2s forwards,
    blink 0.5s step-end infinite alternate;
          animation: shring-animation 2.2s steps(40, end) 2s forwards,
    blink 0.5s step-end infinite alternate;
}

@-webkit-keyframes shring-animation {
  0% {
    background-position: 0 0;
    opacity: 0;
    width: 0;
  }
  1% {
    background-position: 0 0;
    opacity: 1;
    border-right: 1px solid #ff497c;
  }
  50% {
    background-position: 150px 0;
    opacity: 1;
    border-right: 1px solid #ff497c;
  }
  100% {
    background-position: 400px 0;
    opacity: 1;
    border-right: 1px solid #ff497c;
  }
}

@keyframes shring-animation {
  0% {
    background-position: 0 0;
    opacity: 0;
    width: 0;
  }
  1% {
    background-position: 0 0;
    opacity: 1;
    border-right: 1px solid #ff497c;
  }
  50% {
    background-position: 150px 0;
    opacity: 1;
    border-right: 1px solid #ff497c;
  }
  100% {
    background-position: 400px 0;
    opacity: 1;
    border-right: 1px solid #ff497c;
  }
}

@-webkit-keyframes type {
  0% {
    width: 0;
  }
  1%,
  99% {
    border-right: 1px solid #ff497c;
  }
  100% {
    border-right: none;
  }
}

@keyframes type {
  0% {
    width: 0;
  }
  1%,
  99% {
    border-right: 1px solid #ff497c;
  }
  100% {
    border-right: none;
  }
}

@-webkit-keyframes blink {
  50% {
    border-color: transparent;
  }
}

@keyframes blink {
  50% {
    border-color: transparent;
  }
}

